import React, { Component } from 'react';
//Components
import Banner from '../Utilities/Banner';
import Technologies from './Technologies';
class Intro extends Component{    
    render(){
        return(
        <div className="intro-box">
            <div class="intro">
              <div className="about-box">
                <Banner />
                <div id="herman-picture"></div>
              </div>
              <span className="herman-info-box">
                <h2>Hai, I'm Herman and this is my Github Homepage</h2>
                <h5>
                  My aim here is to chronicle my progress as a developer and jot down any thoughts or feelings I may have as I explore the current landscape of the tech. 
                </h5>
                <h5>
                  A bit of context about me: I graduated with a degree in mechanical engineering, and tinkered with many small robotic projects during my time at school. I never dove deep into the field but I always found the mad process of programming little motors and actuators to accomplish whatever our team dreamt up to be a pretty fun and rewarding process. However, I never considered pursuing anything related to computer science professionally, because I always thought that since I didnt start programming until my university days, I was simply too far behind.
                </h5>
                <h5>
                  Fast forward a couple years out of school and I find that I really missed the process of coding and engineering design. So after some deliberation, scoping out what fields are available, I took a leap of faith and dove head first into web-development.
                </h5>
                <h5>
                  It's been an interesting process and I hope to share the projects and assignmnets I've completed throughout my time learning web dev. Some technologies I've worked with now include React, NodeJS, Express, Immutable JS, Sass, Typescript, and PostgreSQL.
                </h5>
                <h5>
                  Initially I was only interested in becoming a Front-End dev, but having taken a deeper dive into JavaScript, I've gotten pretty interested in the Back-End too, and now, my long term goal is to become a full-time Full-Stack developer!
                </h5>
                <h5>Anyways, thanks for stopping by, hope you enjoy!</h5>
              </span>
            </div>
        </div>
        );
    }
}


export default Intro
